import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const StyleLink = styled(Link)`
    display: block;
`

class home extends Component {
    render() {
        return (
            <div>
                <StyleLink to="/clickWave"></StyleLink>
                <StyleLink to="/waterWave">canvas</StyleLink>
                <StyleLink to="/piano"></StyleLink>
                <StyleLink to="/rainDrop"></StyleLink>
                <StyleLink to="/clipCover">canvas</StyleLink>
                <StyleLink to="/springEffect"></StyleLink>
                <StyleLink to="/bulbSwitch">switch</StyleLink>
                <StyleLink to="/numberRoll">3d</StyleLink>
                <StyleLink to="/shootingStar">~</StyleLink>
                <StyleLink to="/dandelion"></StyleLink>
            </div>
        );
    }
}

export default home;



